.btn {
  font-size: 0.6em;
  background-color: transparent;
  border: 1px solid var(--dark-grey);
  padding: 1em;
  margin-bottom: 10px;
  font-weight: 800;
  width: 45%;
  color: var(--light-grey);

  a {
    color: var(--light-grey);
    text-decoration: none;
  }

  a:hover {
    text-decoration: none;
    color: black;
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    font-size: 6px;
    padding: 0.7em 0.5em;
  }
}

.btn:hover {
  color: black;
}