.container {
  display: flex;
  // justify-content: space-between;
  width: 100%;

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    overflow-y: scroll;
  }
}

.productDetailsColumn {
  font-family: var(--modal-font);
  font-weight: 400;
  border-radius: 0px 0px 10px 10px;
  width: 40%;
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  // overflow-y: auto;
  // scrollbar-width: none;
}

.productDetailsColumn::-webkit-scrollbar {
  display: none;
}

.productContent {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow-y: hidden;
}

.titleAndKeySpecsContainer {
  flex-grow: 1;
  // max-height: 50%;
  overflow-y: auto;
  // overflow-y: auto;
}

.titleAndKeySpecsContainer::-webkit-scrollbar {
  display: none;
}

.productContainer {
  overflow: auto;
  scrollbar-width: none;
}

.titleAndKeySpecs {
  text-align: left;
  font-weight: 800;
  cursor: pointer;
  font-size: 0.95rem;
  padding: 1em;

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    padding: 0.7em;
    font-size: 8px;

    p {
      margin-bottom: 0.5em;
    }
  }
}

.variantDropdown {
  border: 1px solid var(--dark-grey);

  .variantTitle {
    margin-bottom: 10px;
    font-size: 1em;
  }
}

.indicationAndAR {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

  img {
    cursor: pointer;
    max-height: 100%;
    max-width: 95px;
    display: block;
    margin: 0 auto;
  }

  table {
    width: 100%;
    line-height: 1;

    th {
      font-weight: normal;
    }

    td {
      display: inline-block;
    }

    td:nth-child(1) {
      width: 15px;
      text-align: center;
    }

    td:nth-child(2) {
      width: 15px;
      text-align: center;
    }
  }

  @media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
    padding: 0.3em 0;
    font-size: 11px;

    img {
      max-height: 40px;
      margin-bottom: 6px;
    }
  }
}

.btnContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: auto;
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  .carouselColumn {
    width: 45%;
    margin: 0;
    display: flex;
    align-items: center;
  }

  .productDetailsColumn {
    // flex: 1;
    width: 55%;
  }
}
