.variantDropdown {
  border: 1px solid var(--dark-grey);
  padding: 10px;
}

p {
  font-size: 1.5em;
}

.productModel {
  margin-bottom: 0;
  display: flex;
  justify-content: space-between;
}

.variantTitle {
  margin-bottom: 10px;
  font-size: 1em;
  color: var(--light-grey);
}

.variantTitle:hover {
  color: black;
}

@media (max-width: 926px) and (max-height: 428px) and (orientation: landscape) {
  p {
    margin-bottom: 0.5em;
  }
}
